import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import {HttpParams} from "@angular/common/http";
import { ApiClient} from "src/autogenerated/client";
@Injectable({
  providedIn: 'root'
})
export class DataService {
  private client: ApiClient.Client;
  private BASE_URL = "https://touti-conversions-api.azurewebsites.net";
  constructor() {
    this.client=new ApiClient.Client(this.BASE_URL);
   }

  public GetIngredients(language="en",apiVersion="1.0"){
    return this.client.ingredients(language,apiVersion);
  }

  /**
   * GetLanguages
   */
  public GetLanguages(apiVersion="1.0") {
    return this.client.languages(apiVersion);
  }

  /**
   * Convert
   */
  public Convert(ingredientId: string, from: string, to: string, quantity: number, language="en",apiVersion="1.0") {
    return this.client.conversions(ingredientId, from, to, quantity, language, apiVersion);
  }
}